import { useContext, useRef } from "react";
import Context from "../../Context/Context";
import { useDispatch } from "react-redux";
import './news.css';
import { Link } from "react-router-dom";

function Post(prop){
    let dispatch = useDispatch()
    const {usersDate} = useContext(Context)

    function openProfile (){
        let user;
        usersDate.forEach(el=>{
            if( el.id === prop.item.id) user = el
        })
        dispatch({type: 'SET_C_P', item: user})
    }

    function createImg(prop){
        if(window.screen.width > 500){
            return(<img src={require(`../../${prop.item.imgUrl}`)} id={prop.item.imgUrl} onClick={()=> openProfile()} className="post_img" alt=""/>)
        }
        else{
            return(<Link to={'/bio'}><img src={require(`../../${prop.item.imgUrl}`)} id={prop.item.imgUrl} onClick={()=> openProfile()} className="post_img" alt=""/></Link>)
        }
    }
    if(typeof (prop.item) != 'string'){
        return(
            <div className="post" id={prop.item.id}>
            <div className="post_body">
                    <div className="person_img">
                      {createImg(prop)}
                    </div>
                    <div className="person_post">
                        <div className="person_name">{prop.item.postName}<div className="person_post_date">{prop.item.postDate}</div>
                        </div>
                        <div className="person_content">
                            {prop.item.postContent}
                        </div>
                    </div>
                </div>
                </div>
        )
    }
    else{
        return(
            <div className="post_time">
                {prop.item}
            </div>
        )
            
    }
    
}
export default Post