import { useContext } from "react"
import Context from "../../Context/Context"
import move from "../../hooks/move";
import { useDispatch } from "react-redux";
import './nomad.css';
import { Link } from "react-router-dom";
import { useNavigate } from "react-router-dom";


function Nomad(){
    let navigate = useNavigate()
    let dispatch = useDispatch()
    const {setIsContacts, currentItem, currentUser, setMobileBio, screenWidth} = useContext(Context)
    if(!currentUser.email){
        navigate('/')
    }
    let log;
    currentUser.email ? log = 'Profile' : log = 'Log in';
    
    function setter(){
        if(!currentUser.email && log === 'Log in'){
            dispatch({type:'REMOVE_CT'})
            dispatch({type: 'REMOVE_C_P', item: {}})
            move(false, currentItem.loging, currentItem.hiden)
        }
        else{
            dispatch({type:'REMOVE_CT'})
            dispatch({type: 'REMOVE_C_P'})
        }
    }

    return(
        <div className="nomad_list ">
            <div className="back_menu" onClick={()=> dispatch({type:'REMOVE_CT'})}>Back</div>
            <div className="nomad">Nomad</div>
            {screenWidth <= 500? <Link to={'/bio'}><button className="enter_but" onClick={(e)=> setter()}>{log}</button></Link>: ''}
        </div>
    )
}
export default Nomad