import React, { useContext } from 'react'
import Context from '../../Context/Context';
import { Link } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import './ex_but.css'

function ExBut (){
    const dispatch = useDispatch()
    let navigate = useNavigate()
    const {setCurrentUser, setIsTrade, setIsReg, setCurrentPerson, currentPerson} = useContext(Context)
    let exiteCl = `exite_but_user`;
    let exiteText = `Close`
    if (!currentPerson.id) {
        exiteCl = `exite_but`;
        exiteText = `Exite`;
    }
    function Exite(e){
        if(e.target.textContent == `Exite`){

            dispatch({type: 'REMOVE_C_U'})
            dispatch({type: 'REMOVE_TR'})
            dispatch({type: 'REMOVE_IR'})
            
            navigate('/')
            
        }
        else{
            dispatch({type: 'REMOVE_C_P'})
            dispatch({type: 'SET_I_P'})
            navigate('/general')

        }

    }
  return (
    <>
    <div className={exiteCl} onClick={ e => Exite(e)}>{exiteText}</div>
    </>
  )
}
export default ExBut